<template>
	<!-- 酒店管家-派单中心页面 -->
	<page-meta :page-style="'overflow:'+(show?'hidden':'visible')"></page-meta>
	<view class="order">
		<view class="orderItem" v-for="(item,index) in orderList" :key="index">
			<view class="orderTime">
				{{item.time}}
			</view>
			<view class="orderContent boxshadow">
				<view :class="['tip',{wait:item.status == 1},{finish:item.status == 2}]">
					{{item.status == 1?'待派单':item.status == 2?'已完成':'待审核'}}
				</view>
				<view class="content-top">
					<view class="roomInfo">
						<view class="roomNumber">
							{{item.roomNumber}}
						</view>
						<view class="hotelName">
							{{item.hotel}}
						</view>
					</view>
					<view class="ownerContent">
						<view class="owner">
							<view class="owner-row">发布人：<span style="color: #999999;font-weight: normal;">{{item.owner}}</span></view>
							<view class="owner-row">发布人电话：<span style="color: #999999;font-weight: normal;">{{item.phone}}</span></view>
						</view>
					</view>
				</view>
			</view>
			<view class="content-bottom">
				<view class="remark">
					保洁阿姨姓名：{{item.auntName}}
				</view>
				<image @click="telephone(item.auntPhone)" style="width: 68rpx;height: 68rpx;" src="/static/phone.png"></image>
			</view>
		</view>
		<u-picker :show="show" :columns="columns" keyName="name" :closeOnClickOverlay="true" @close="show = false" @confirm="confirmServe"></u-picker>
		<view style="height: 80rpx;"></view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				show:false,
				columns:[[{name:'张阿姨'},{name:'李阿姨'},{name:'陈阿姨'}]],
				pageNo:1,
				total: 10,
				overContent: true, // 节流
				orderList: [{
					time:'2022-05-06 15:30',
					roomNumber:1120,
					owner:'陈腾',
					phone:'1985585888',
					hotel:'希尔顿酒店',
					auntName:'沈菲',
					auntPhone:'13848411777',
					status:1, // 0 待审核 1待派单 2已完成
				},{
					time:'2022-05-06 15:30',
					roomNumber:1120,
					owner:'陈腾',
					phone:'1985585888',
					hotel:'希尔顿酒店',
					auntName:'沈菲',
					auntPhone:'13848411777',
					status:0,
				},{
					time:'2022-05-06 15:30',
					roomNumber:1120,
					owner:'陈腾',
					phone:'1985585888',
					hotel:'希尔顿酒店',
					auntName:'沈菲',
					auntPhone:'13848411777',
					status:2,
				}],
			}
		},
		onReachBottom() {
			// 触底的时候请求数据，即为上拉加载更多
			let allTotal = this.pageNo * 10
			if (allTotal < this.total && this.overContent) {
				this.overContent = false;
				this.pageNo++;
				// this.getOrderList();
			}
		},
		created() {
			// this.getOrderList();
		},
		methods: {
			confirmServe(value){
				console.log(value.value[0].name)
				uni.showToast({
					title:`选中了${value.value[0].name}`,
					icon:"none",
				})
				this.show = false
			},
			showServer(item){
				if(item.status != 1)return
				this.show = true
			},
			async getOrderList(){
				
			},
			tapButton(item){
				if(item.status == 1){
					this.toPath(`/pages/order/repair?type=1`)
				}else if(item.status == 0){
					
				}
			},
			// 打电话
			telephone(phone){
				let that = this;
				const res = uni.getSystemInfoSync();
				if (res.platform === 'ios') {
					uni.makePhoneCall({
						phoneNumber: phone,
						success() {
						},
						fail() {
						}
					})
				} else {
					uni.showActionSheet({
						itemList: [phone, '呼叫'],
						success(res) {
							console.log(res);
							if (res.tapIndex === 1) {
								uni.makePhoneCall({
									phoneNumber: phone
								})
							}
						}
					})
				}
			},
			toPath(url){
				uni.navigateTo({
					url:url
				})
			},
		}
	}
</script>

<style>
	page {
		background-color: #FAF9FE;
	}
</style>
<style lang="less" scoped>
	.order {
		font-family: Arial-BoldItalicMT, Arial;
		padding: 0 30rpx;
		.orderItem{
			border-radius: 20rpx;
			font-size: 24rpx;
			padding-top: 10rpx;
			.orderTime{
				color: #8CAFEE;
				margin: 20rpx 10rpx;
			}
			.orderContent{
				border-radius: 20rpx;
				overflow: hidden;
				position: relative;
				.tip{
					font-size: 28rpx;
					font-family: YouSheBiaoTiHei;
					padding: 8rpx 40rpx;
					color: #FFFFFF;
					background: #53C79D;
					border-radius: 0px 0px 0px 20px;
					position: absolute;
					right: 0;
					top: 0;
				}
				.wait{
					background: #E0BF20;
				}
				.finish{
					background: #2066E0;
				}
				.content-top{
					background-color: #FFFFFF;
					padding: 36rpx 40rpx 30rpx 40rpx;
					.roomInfo{
						display: flex;
						align-items: center;
						.roomNumber{
							font-size: 32rpx;
							color: #FFFFFF;
							background-color: #2066E0;
							padding: 6rpx 12rpx;
							transform: skewX(-15deg);
							border-radius: 4rpx;
							margin-right: 20rpx;
						}
						.hotelName{
							width: 48%;
							font-weight: bold;
							font-size: 18px;
							font-family: YouSheBiaoTiHei;
							color: #000000;
							overflow: hidden;
							text-overflow: ellipsis;
							white-space: nowrap;
						}
						
					}
					.ownerContent{
						display: flex;
						justify-content: space-between;
						align-items: center;
						.owner{
							font-size: 28rpx;
							.owner-row{
								color: #000000;
								font-weight: bold;
								margin-top: 12rpx;
							}
						}
						.owner-btn{
							font-size: 36rpx;
							color: #2066E0;
							font-weight: bold;
						}
						.stop{
							color: #999999;
						}
					}
				}
			}
			.content-bottom{
				width: 80%;
				display: flex;
				justify-content: space-between;
				align-items: center;
				color: #333333;
				padding: 24rpx 40rpx 20rpx 20rpx;
				background: #DFEBFF;
				margin-left: 40rpx;
				border-radius: 0 0 20rpx 20rpx;
				.remark{
					width: 70%;
				}
			}
		}
	}
</style>
